@import "../../variables.scss";

.navBar {
  max-width: 100vw;
  width: 100vw;
  height: 40px;
  border: $primary-border;
  background-color: $secondary-background-color;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: $base-padding;
  overflow: hidden;

  .header {
    margin: $base-margin;
    color: $secondary-color;
    font-size: 30px;
    text-align: left;
    font-family: sans-serif;
    span {
      color: $secondary-text;
    }
  }

  .github {
    color: $secondary-text;
    padding: 0 $base-padding;
    color: $tertiary-text;
    font-size: 20px;
    font-weight: 600;
    p {
      color: $secondary-text;
    }
    span {
      margin: $base-margin/2;
      white-space: nowrap;
    }
    a {
      color: $secondary-text;
      margin-left: 5px;
      &:hover {
        color: $secondary-color;
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .navBar {
    height: 80px;
    .header {
      font-size: 18px;
    }

    .github {
      p {
        font-size: 16px;
        text-align: right;
      }
    }
  }
}
